export type JKFunction = (...args: any[]) => any;

export type JKObject = { [key: string]: any };

export type JKArray = any[];

export type JKGeneralObject = JKObject | JKArray;

export type JKObjectByFunc = { [key: string]: JKFunction };

export type JKConstructor<T> = new (...args: any[]) => T;

export type API_METHOD = 'get' | 'post' | 'put';

export type ROUTER_TYPE = 'http' | 'firebase';

export interface FETCHER_PARAMS {
  name: string; 
  method: API_METHOD; 
  fetchParams: JKObject; 
  errorHandler: JKFunction; 
  selfHandler: JKFunction;
  apiOptions?: JKObject;
}

export interface FETCH_RUN_PROPS extends FETCHER_PARAMS{
  apiType: ROUTER_TYPE; 
}

export interface OUTPUT_PROPS {
  response: JKObject | null;
  headers?: JKObject | null;
  request?: JKObject | null;
  success: boolean; 
  name: string; 
  errorHandler?: JKFunction
  httpStatusCode: number
}

export enum SETUP_TYPES {
  HTTP = 'http',
  FIREBASE = 'firebase',
}

export interface MOCK_PROPS {
  data: JKObject;
  status?: number | string;
}

export interface SETUP_PROPS {
  errorHandler?: JKFunction;
  protocol?: JKObject;
  mock?: {
    [key: string]: MOCK_PROPS;
  }
  config?: {
    [key in SETUP_TYPES]: JKObject
  },
  engine: {
    [key in SETUP_TYPES]?: JKFunction
  }
}

export enum PROTOCOL_OPTION {
  REQUEST = 'request',
  RESPONSE = 'response'
}

export enum PROTOCOL_PARAMS_TYPES {
  BOOL = 'bool',
  STRING = 'string',
  NUMBER = 'number',
  OBJECT = 'object',
  ARRAY = 'array',
}

export interface PROTOCOL_CONTEXT {
  name?: string;
  type?: PROTOCOL_PARAMS_TYPES;
  rename?: string;
  default?: string;
  must?: boolean;
  children?: PROTOCOL_CONTEXT[];
}

export interface PROTOCOL_PAYLOAD {
  [key: string]: {
    [key in PROTOCOL_OPTION]: PROTOCOL_CONTEXT[]
  }
}