import { JKArray, SETUP_PROPS, ROUTER_TYPE } from '../interface';
import size from './size';
import Router from '../model/RouterModel';
import Api from '../model/ApiModel';
import Protocol from '../model/ProtocolModel';
import Mock from '../model/MockModel';

const setupJkApi = (argvs: SETUP_PROPS) => {
  /** 若定義過就不用再跑一次，避免 rerender */
  if (size(Api.router) > 0) {
    return;
  }

  const engines = argvs?.engine;
  const protocol = argvs?.protocol;
  const mock = argvs?.mock;
  const config = argvs?.config;
  const errorHandler = argvs?.errorHandler;
  
  if (errorHandler) Router?.config?.setErrorHandler(errorHandler);
  if (size(engines) < 1) return;
  if (mock && size(mock) > 0) {
    Mock.save(mock);
  }
  if (protocol && size(protocol) > 0) {
    Protocol.save(protocol);
  }
  if (config && size(config) > 0) {
    Router?.config?.setExternalData(config);
  }

  /** run api engine */
  Object.entries(engines).forEach((engine: JKArray) => {
    const type: ROUTER_TYPE = engine?.[0];
    const fn = engine?.[1];
    
    Router.config.switch(type);
    fn();
  });
};

export default setupJkApi;