import { GET, POST } from '../data/constants';
import { API_METHOD, JKObject, PROTOCOL_PAYLOAD, PROTOCOL_OPTION } from '../interface';
import validator from '../util/validator';

const requestFormat: JKObject = {
  [GET]: 'urlParams',
  [POST]: 'body'
}

class ProtocolModel {
  pattern: PROTOCOL_PAYLOAD = {};

  save(pattern: PROTOCOL_PAYLOAD) {
    this.pattern = pattern;
  }

  request(name: string, method: API_METHOD, fetchParams: JKObject) {
    const targetPattern = this.pattern?.[name]?.request;
    const formatData = fetchParams?.[requestFormat?.[method] || 0] || {};
    if (!targetPattern) return;
    
    const errorPayload = validator(PROTOCOL_OPTION.REQUEST, targetPattern, formatData);
    errorPayload && this.errorHandler(PROTOCOL_OPTION.REQUEST, errorPayload);
  }

  response(name: string, success: boolean, data: JKObject, headers: JKObject, response: JKObject) {
    const targetPattern = this.pattern?.[name]?.response;
    if (targetPattern && success) {
      const errorPayload = validator(PROTOCOL_OPTION.RESPONSE, targetPattern, data);
      errorPayload && this.errorHandler(PROTOCOL_OPTION.RESPONSE, errorPayload);
    } 
    
    if (!success) {
      return {
        error: true,
        data: {
          payload: data,
          jkApiName: name,
        }, 
        headers,
        response
      }
    }

    return {
      error: false,
      data,
      headers,
      response
    };
  }

  errorHandler(type: string, payload: JKObject) {
    throw new Error(
      `[Api] ${type} params is not allowed \n ${JSON.stringify(
        payload,
        null,
        2
      )}`,
    );
  }
}

const protocol = new ProtocolModel();

export default protocol;

