import { RouterModel } from './RouterModel';
import Protocol from './ProtocolModel';
import Mock from './MockModel';
import Fetcher from './FetchModel';
import Api from './ApiModel';
import { API_METHOD, ROUTER_TYPE, JKFunction, JKArray, JKObject } from '../interface';
import { API_HTTP } from '../data/constants';
import fillUrl from '../util/fillUrl';
import output from '../util/output';

class SendModel {
  run(routerInstance: RouterModel, method: API_METHOD, ...args: any[]) {
    try {
      const data = this.refine(routerInstance, ...args);

      if (Api.router?.[data.name])
        throw new Error(`[Api Router] Cannot redefine router name: ${data.name}`);
      
      Api.router[data.name] = (...args: JKArray) => {
        let fetchParams: JKObject = data.paramsHandler ? data.paramsHandler(...args) : {};

        if (!fetchParams?.url) fetchParams.url = data.url;
        
        fetchParams = fillUrl(fetchParams, data.apiType === API_HTTP);
        
        Protocol.request(data.name, method, fetchParams);

        const mockData = Mock.runMock(data.name);
        
        if(mockData) {
          return new Promise(async (resolve) => {
            resolve(await output({
              response: mockData?.data,
              success: mockData?.success,
              name: data.name,
              errorHandler: data?.errorHandler
            }))
            // resolve(Protocol.response(data.name, mockData?.success, mockData?.data));
          })
        }
        
        return Fetcher.run({ 
          ...data,
          fetchParams, 
          method, 
        });
      }
    } catch (e) {
      console.error(e);
    }
  }

  refine(routerInstance: RouterModel, ...args: any[]) {
    const apiType: ROUTER_TYPE = routerInstance?.config?.type || API_HTTP;
    const errorHandler = (params: JKObject) => routerInstance?.config?.errorHandler(params);
    const paramsHandler = routerInstance?.config?.paramsHandler;
    const apiOptions = routerInstance?.config?.options || {};
    const name = args?.[0];
    const url = args?.[1];
    const selfHandler: JKFunction = args?.[2];
    
    return {
      apiType,
      errorHandler,
      paramsHandler,
      apiOptions,
      name,
      url,
      selfHandler
    };
  }
}

const send = new SendModel();

export default send;
