import { JKObject } from "../interface";
import urlAddQueryStr from './urlAddQueryStr';
import size from './size';

const fillUrl = (fetchParams: JKObject, autoAddQueryStr: Boolean = true) => {
  let url = fetchParams?.url;
  const urlParams = fetchParams?.urlParams || [];
  if (!url || !size(urlParams)) return fetchParams;

  const matches = url.match(/\{([A-Za-z0-9_:\\,|@\s]*)\}/gi);

  if (size(matches)) {
    matches.forEach((match: string) => {
      const col = match.replace(/{|}/g, '');
      const arg =urlParams?.[col];
      url = url.replace(`{${col}}`, arg);
      delete urlParams[col];
    });
  }

  // 將多餘的參數加在 url query string
  if (size(urlParams) && autoAddQueryStr) {
    Object.keys(urlParams).forEach(col => {
      url = urlAddQueryStr(url, col, urlParams[col]);
    });
  }
  // eslint-disable-next-line no-param-reassign
  fetchParams.url = url;
  return fetchParams;
}

export default fillUrl;