import { JKObject, MOCK_PROPS } from '../interface';
import { pathStr } from '../util/fp';

class MockModel {
  public stories: { [key: string]: MOCK_PROPS } = {};

  public save(mock: JKObject) {
    const apis = Object.keys(mock) || [];
    this.stories = apis.reduce((acc: JKObject, cur: string) => {
      const statusStr = (mock[cur]?.status || 200).toString();
      const data = mock[cur]?.data || {};
      acc[cur] = {
        status: statusStr,
        data: pathStr(statusStr)(data)
      };
      return acc;
    }, {});
  }

  public runMock(apiName: string) {
    const payload = this.stories?.[apiName];
    if (!payload) return null;

    return {
      success: payload.status && !!payload.status.toString().match(/200/) || false,
      data: JSON.parse(JSON.stringify(payload?.data))
    }
  }
}

const Mock = new MockModel();

export default Mock;
