import indexOf from "ramda/src/indexOf";
import { ALLOW_TYPES } from "../data/constants";
import { JKObject, PROTOCOL_CONTEXT, PROTOCOL_OPTION } from "../interface";
import size from "./size";

/**
 * validator
 */
const checkAllowType = (type: string, data: any) => {
  if (indexOf(type, Object.values(ALLOW_TYPES)) === -1) return "no exist";

  if (type === ALLOW_TYPES.BOOL && typeof data !== "boolean")
    return ALLOW_TYPES.BOOL;
  if (type === ALLOW_TYPES.STRING && typeof data !== "string" && data !== null)
    return ALLOW_TYPES.STRING;
  if (type === ALLOW_TYPES.NUMBER && typeof data !== "number" && data !== 0)
    return ALLOW_TYPES.NUMBER;
  if (type === ALLOW_TYPES.OBJECT && typeof data !== "object")
    return ALLOW_TYPES.OBJECT;
  if (type === ALLOW_TYPES.ARRAY && !Array.isArray(data))
    return ALLOW_TYPES.ARRAY;

  return "ok";
};

/**
 * 重定義外傳參數
 */
const format = (
  value: JKObject,
  data: JKObject,
  structType: PROTOCOL_OPTION,
  prename = ""
) => {
  let name = value?.name || false;
  const must = value?.must || false;
  const rename = value?.rename || null;
  const type = value?.type || null;
  const d4 = value?.default;

  if (structType === PROTOCOL_OPTION.REQUEST && rename) name = rename;

  const showName = rename || name;

  if (name) {
    let target = data && data[name] !== undefined ? data[name] : null;

    const children = value?.children || [];
    if (children.length) {
      children.forEach((childVal: JKObject) => {
        const targets = Array.isArray(target) ? target : [target];
        targets.forEach((t) => format(childVal, t, structType, name));
      });
    }

    if (target === null) {
      if (must) {
        error.call(showName, { must: true });
        return;
      }

      if (rename && !prename && data[name] !== undefined) {
        data[rename] = target;
        delete data[name];
      }
      if (data && d4 !== undefined) (data[name] = d4), (target = data[name]);
      else return;
    }

    if (!name && type !== ALLOW_TYPES.ARRAY) {
      error.call(showName, { name: showName, must: true });
      return;
    }

    // attr: type
    if (type) {
      const allowType = checkAllowType(type, target);
      if (allowType !== "ok") {
        error.call(showName, { type: allowType, params: target });
      }
    }
    // attr: rename
    if (data && rename) {
      if (structType === PROTOCOL_OPTION.REQUEST) {
        data[value?.name] = data[rename];
        delete data[rename];
      } else {
        if (typeof data === "object") {
        }
        data[rename] = data[name];

        if (rename !== name) delete data[name];
      }
    }
    return;
  }

  if (type) {
    const allowType = checkAllowType(type, data);
    if (allowType !== "ok") {
      error.call(rename || prename, { type: allowType, params: data });
    }
  }
};

const error = {
  payload: <JKObject>[],
  init: () => {
    error.payload = [];
  },
  call: (name: string, others: JKObject) => {
    error.payload.push({
      name,
      ...others,
    });
  },
};

/**
 * 驗證
 * @param {class} instance  | Refiner class
 * @param {any} data        | params
 * @returns
 */
const validator = (
  type: PROTOCOL_OPTION,
  pattern: PROTOCOL_CONTEXT[],
  data: JKObject | JKObject[]
) => {
  error.init();

  const name = "LayerFirstArray";
  let newPattern = pattern;
  let newData: JKObject = data;

  const isFirstLayerArray =
    Array.isArray(data) &&
    pattern.length === 1 &&
    pattern?.[0].type === ALLOW_TYPES.ARRAY;
  // 第一層是 Array
  if (isFirstLayerArray) {
    newPattern = [
      {
        ...pattern?.[0],
        name,
      },
    ];
    newData = { [name]: data };
  }

  newPattern.forEach((value) => {
    format(value, newData, type);
  });

  isFirstLayerArray && (data = newData?.[name]);

  if (size(error.payload)) {
    return error.payload;
  }

  return null;
};

export default validator;
