import { GET } from '../data/constants';
import { FETCHER_PARAMS } from '../interface';
import httpRequest from '../request/http';
import output from '../util/output';
import size from '../util/size';

const httpFetcher = (args: FETCHER_PARAMS) => {
  const name = args?.name;
  const method = args?.method;
  const errorHandler = args?.errorHandler;
  const resultStatusHandler = args?.apiOptions?.resultStatusHandler;
  const { url, body = null, headers = {}, signal } = args?.fetchParams || {};

  return new Promise(async (resolve, reject) => {
    if (!url) {
      reject();
    }
    /** 開始執行 api request */
    const options = {
      method,
      body: JSON.stringify({}),
      headers,
      signal,
    };
    if (method !== GET && body && size(body)) {
      options.body = JSON.stringify(body);
    }

    let response;
    try {
      response = await httpRequest(`${url}`, options);
    } catch (e: any) {
      const result = await output({
        response: null,
        success: false,
        name,
        errorHandler,
        request: options,
        httpStatusCode: (e && e?.status) || null,
      });
      resolve(result);
      return;
    }

    let payload = null;
    if (response?.data) {
      try {
        payload =
          response.data instanceof Promise
            ? await response.data
            : response.data;
      } catch (e) {}
    }

    const resultStatusParams = {
      status: response.code,
      data: payload,
    };

    const isSuccess = resultStatusHandler
      ? Boolean(resultStatusHandler(resultStatusParams))
      : response.code >= 200 && response.code < 300;

    const httpStatusCode = response.code;

    const result = await output({
      response: payload,
      headers: response?.headers,
      httpStatusCode,
      success: isSuccess,
      name,
      errorHandler,
      request: options,
    });

    resolve(result);
  });
};

export default httpFetcher;
