import { ERROR_CODE } from "../data/constants";
import { OUTPUT_PROPS } from "../interface";
import Protocol from "../model/ProtocolModel";

const output = async (args: OUTPUT_PROPS) => {
  const payload = args?.response || { Result: ERROR_CODE.SYSTEM_OUT };
  const headers = args?.headers || {};
  const success = args?.success || false;
  const jkApiName = args?.name || "";
  const errorHandler = args?.errorHandler;
  const request = args?.request;
  const httpStatusCode = args?.httpStatusCode;

  const response = { httpStatusCode };

  if (!success && errorHandler)
    errorHandler({ payload, jkApiName, request, response });

  return {
    ...Protocol.response(jkApiName, success, payload, headers, response),
  };
};

export default output;
