import Send from './SendModel';
import { GET, POST, PUT } from '../data/constants';
import { JKArray, JKFunction, ROUTER_TYPE, API_METHOD, JKObject } from '../interface';

export interface RouterOptions {
  resultStatusHandler?: (response: JKObject) => boolean
}

class RouterConfigModel {
  type?: ROUTER_TYPE;
  externalData?: JKObject;

  errorHandler: JKFunction = () => ({});
  paramsHandler?: JKFunction = () => ({});

  options?: RouterOptions = {}

  switch(val: ROUTER_TYPE) {
    this.type = val;
  }
  setExternalData(data: JKObject) {
    this.externalData = data;
  }
  setErrorHandler(handler: JKFunction) {
    this.errorHandler = handler;
  }
  setParamsHandler(handler: JKFunction) {
    this.paramsHandler = handler;
  }
  setOptions(options: RouterOptions) {
    this.options = options;
  }
}

class RouterModel {
  config: RouterConfigModel;

  constructor() {
    this.config = new RouterConfigModel();
  }

  rest(method: API_METHOD, ...args: JKArray) {
    Send.run(this, method, ...args);
  }

  group(routesFn: JKFunction, paramsFn?: JKFunction | null, options?: RouterOptions) {
    paramsFn && this.config.setParamsHandler(paramsFn);
    options && this.config.setOptions(options);
    routesFn();
  }

  post(...args: JKArray) {
    this.rest(POST, ...args);
  }
  get(...args: JKArray) {
    this.rest(GET, ...args);
  }
  put(...args: JKArray) {
    this.rest(PUT, ...args);
  }
}

const Router = new RouterModel();

export { RouterModel };
export default Router;
