import { GET } from '../data/constants';
import { JKObject, JKArray } from '../interface';

const parseHeaders = (data: JKArray) => {
  const res: JKObject = {};
  data.forEach((value, key) => {
    res[key] = value;
  })
  return res;
}

const parseJSON = (response: JKObject) => {
  return {
    code: response.status,
    data:
      response.headers.get('Content-Length') === '0' ? null : response.json(),
    headers: response.headers && parseHeaders(response.headers),
  };
}

export default function request(url: string, options?: JKObject) {
  if (options) {
    const method = options?.method;
    if (method === GET && options?.body) {
      delete options.body;
    }
  }

  return fetch(url, options || {}).then(parseJSON);
}


