import { JKObjectByFunc, ROUTER_TYPE, FETCH_RUN_PROPS } from '../interface';
import defaultFetcher from '../fetcher';

class FetchModel {
  private apiFetcher: JKObjectByFunc = {};

  set fetcher(fetches: JKObjectByFunc) {
    this.apiFetcher = { ...this.apiFetcher, ...fetches };
  }

  get fetcher() {
    return this.apiFetcher;
  }

  run({ 
    name, 
    method, 
    apiType, 
    fetchParams, 
    errorHandler, 
    selfHandler,
    apiOptions 
  }: FETCH_RUN_PROPS) {
    const fetcher = { ...defaultFetcher, ...this.fetcher };
    if (fetcher?.[apiType]) return fetcher[apiType]({ name, method, errorHandler, fetchParams, selfHandler, apiOptions });
    return null;
  }
}

const fetcher = new FetchModel();

export default fetcher;
